.flexRow {
	display: -ms-flexbox;
	display: flex;
}

.flexItem {
	margin: 10px 60px 30px 20px;
	padding: 10px 10px;
}

.dropdown {
	position: relative;
	display: inline-block;
	text-decoration: underline;
}

.dropdown-content {
	display: none;
	position: absolute;
	background-color: #FECD08;
	min-width: 70px;
	padding: 0px 10px;
	z-index: 1;
}

.dropdown:hover .dropdown-content {
	display: block;
}

.dropdown:hover {
	background-color: #004511; /* NDSU colors! */
	color: #FECD08;
}


/*****INPUT PAGE*****/
.txtBox {
	width: 45px;
}

.flexRowMainContent {
	display: -ms-flexbox;
	display: flex;
}

.flexRowInput {
	display: -ms-flexbox;
	display: flex;
}

.flexItemAxleCount {
	padding: 5px 0px 5px 0px;
	width: 120px;
}

.flexItemCheckBox {
	padding: 0px 0px 0px 0px;
	width: 90px;
}

.flexItemAxleDetails {
	padding: 5px 0px 5px 0px;
	width: 80px;
}

/***** MODAL STYLES *****/
/* style text to look like a link */
.link {
	cursor: pointer;
	color: blue;
	font-weight: bold;
	text-decoration:underline;
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
}
.link:hover {
	text-decoration:none;
	text-shadow: 1px 1px 1px #555;
}

/* The Modal (background) */
.modal {
	display: none; /* Hidden by default */
	position: fixed; /* Stay in place */
	z-index: 1; /* Sit on top */
	padding-top: 100px; /* Location of the box */
	left: 0;
	top: 0;
	width: 100%; /* Full width */
	height: 100%; /* Full height */
	overflow: auto; /* Enable scroll if needed */
	background-color: rgb(0,0,0); /* Fallback color */
	background-color: rgba(0,0,0,0.9); /* Black w/ opacity */
}

/* Modal Content (Image) */
.modal-content {
	margin: auto;
	display: block;
	width: 80%;
	max-width: 700px;
}

/* Caption of Modal Image (Image Text) - Same Width as the Image */
.caption {
	font-size: 30px;
	margin: auto;
	display: block;
	text-align: center;
	color: #ccc;
	padding: 10px 0;
}

/* Add Animation */
.modal-content, .caption { 
	-webkit-animation-name: zoom;
	-webkit-animation-duration: 0.6s;
	animation-name: zoom;
	animation-duration: 0.6s;
}
@-webkit-keyframes zoom {
	from {-webkit-transform:scale(0);transform:scale(0)} 
	to {-webkit-transform:scale(1);transform:scale(1)}
}
@keyframes zoom {
	from {-webkit-transform:scale(0);transform:scale(0)} 
	to {-webkit-transform:scale(1);transform:scale(1)}
}

/* The Close Button */
.close {
	position: absolute;
	top: 15px;
	right: 35px;
	color: #f1f1f1;
	font-size: 40px;
	font-weight: bold;
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
}
.close:hover{
	color: #bbb;
	cursor: pointer;
}

/* 100% Image Width on Smaller Screens */
@media only screen and (max-width: 700px){
	.modal-content {
			width: 100%;
	}
}
/***** END MODAL STYLES *****/
/***** END INPUT PAGE *****/



